body {
  font-family: Dm sans, sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.container {
  width: 100vw;
  margin: 0 auto;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

.button {
  max-height: 44px;
  text-align: center;
  border: none;
  border-radius: 25px;
  padding: 0 37px;
}

.button--white {
  color: #d41443;
  background-color: #fff;
}

.button--white:hover, .button--white:focus {
  cursor: pointer;
  background-color: #ecebeb;
}

.button--main {
  color: #fff;
  background-color: #d41443;
}

.button--main:hover, .button--main:focus {
  cursor: pointer;
  background-color: #c7133e;
}

.button--circle {
  color: #d41443;
  min-width: 16px;
  min-height: 16px;
  background-color: #fff;
  border: none;
  border-radius: 50%;
}

.button--circle:hover, .button--circle:focus {
  cursor: pointer;
  background-color: #ecebeb;
}

.menu-container {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  background-color: #e17992;
  padding-top: 60px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

@media screen and (min-width: 768px) {
  .menu-container {
    width: 33vw;
  }
}

@media screen and (min-width: 768px) and (min-width: 1280px) {
  .menu-container {
    display: none;
  }
}

.menu-container__btn-wrapper {
  width: calc(100% - 21px);
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .menu-container__btn-wrapper {
    width: calc(100% - 34px);
  }
}

.menu-container__btn {
  width: 100%;
  padding: 1em;
  font-size: 5vw;
  font-weight: 600;
}

@media screen and (min-width: 768px) {
  .menu-container__btn {
    font-size: 16px;
  }
}

.menu-container__items {
  margin-top: 5.9vw;
}

.menu-container__item {
  border-top: 1px solid #e18292;
  border-bottom: 1px solid #e18292;
  padding: .86em 0;
  display: block;
}

.menu-container__link {
  color: #fff;
  margin-left: 1.5em;
  font-size: 4.375vw;
  font-weight: 700;
}

@media screen and (min-width: 768px) {
  .menu-container__link {
    font-size: 16px;
  }
}

.menu-container__link:hover, .menu-container__link:focus {
  color: #d41443;
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-toggle {
  background-color: #0000;
  border: none;
}

@media screen and (min-width: 1280px) {
  .menu-toggle {
    display: none;
  }
}

.menu-toggle__btn {
  fill: #d41443;
  color: #d41443;
}

.menu-toggle__btn-close {
  color: #fff;
  position: absolute;
  top: 23px;
  right: 28px;
}

@media screen and (min-width: 768px) {
  .menu-toggle__btn-close {
    right: 34px;
  }
}

.open-button {
  cursor: pointer;
  position: absolute;
  top: 100px;
}

.main-backdrop {
  height: 100%;
  width: 100%;
  visibility: visible;
  z-index: 100;
  background: #0000004d;
  transition-property: visibility;
  animation-duration: 2s;
  animation-timing-function: ease-in;
  animation-direction: normal;
  animation-fill-mode: forwards;
  position: absolute;
  top: 0;
  left: 0;
}

.is-hidden {
  visibility: hidden;
  pointer-events: none;
  opacity: 0;
}

.modal {
  width: 100vw;
  max-width: 450px;
  visibility: visible;
  transition-property: visibility opacity;
  z-index: 999;
  background: #fff;
  border: 1px solid #ffb8ca;
  border-radius: 4px;
  margin: 30px auto auto;
  transition-duration: 2s;
  overflow-x: hidden;
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

.modal__heading {
  letter-spacing: .04em;
  background-color: #ffb8ca;
  justify-content: space-between;
  font-family: Titan One, sans-serif;
  font-size: 38px;
  font-weight: 400;
  line-height: 44px;
  display: flex;
}

.modal__text {
  margin: 0 0 0 30px;
  padding: 20px 0;
}

.modal__order {
  color: #d41443;
}

.modal__order-white {
  color: #fff;
}

.modal__close {
  width: 30px;
  height: 30px;
  opacity: .7;
  fill: #520808;
  cursor: pointer;
  background-color: #fff;
  border: 2px solid #d41443;
  border-radius: 50%;
  margin-top: 5px;
  margin-right: 5px;
}

.modal__icon {
  margin-bottom: 16px;
}

.choose {
  color: #e29aac;
  letter-spacing: .04em;
  margin-top: 15px;
  margin-bottom: 0;
  margin-left: 15px;
  font-family: Titan One, sans-serif;
  font-size: 22px;
  font-weight: 400;
  line-height: 44px;
}

.form {
  margin-left: 25px;
}

.form__element {
  letter-spacing: .03em;
  color: #20191b9d;
  padding-top: 14px;
  padding-bottom: 14px;
  font-family: Dm sans, sans-serif;
  font-size: 14px;
  font-weight: 700;
  display: inline-block;
}

.form__label-first {
  font-size: 13px;
}

.form__item {
  letter-spacing: .03em;
  color: #20191bab;
  padding: 5px;
  font-family: Dm sans, sans-serif;
  font-size: 13px;
}

.form__label-second {
  margin-top: 10px;
}

.form__input-position {
  width: 150px;
  margin-left: 15px;
}

.form__label-third {
  align-items: center;
  margin-top: 10px;
  display: flex;
}

.form__label-third input[type="range"] {
  width: 200px;
}

.form__label-third input[type="range"]::-moz-range-track {
  width: 100%;
  height: 6px;
  cursor: pointer;
  background: linear-gradient(to right, #e29aac, #4baccac0);
  border-radius: 50px;
  box-shadow: 1px 1px 1px #000, 0 0 2px #0d0d0d;
}

.form__label-third input[type="range"]::-moz-range-thumb {
  height: 8px;
  width: 16px;
  cursor: pointer;
  background: #fff;
  border: 1px solid #00000098;
  border-radius: 50px;
  box-shadow: 1px 1px 1px #000, 0 0 1px #0d0d0d;
}

.form__list {
  max-width: 370px;
  flex-wrap: wrap;
  justify-content: center;
  gap: 5px;
  margin: auto auto 30px 10px;
  padding: 0;
  list-style: none;
  display: flex;
}

.form__grid {
  width: 87.5px;
  flex-basis: calc(33.3333% - 3.33333px);
}

.data {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.data__label {
  color: #41315e96;
  letter-spacing: .02em;
  padding-top: 14px;
  padding-bottom: 14px;
  font-family: Titan One, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.data__container {
  height: 45px;
  width: 280px;
  border: 2px solid #21212133;
  border-radius: 50px;
  padding-left: 15px;
  font-family: Dm sans, sans-serif;
  font-size: 14px;
  transition-property: border-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.data .pink:hover, .data .pink:focus, .data .pink:active {
  border-color: #cc7197c7;
  outline: none;
}

.data .green:hover, .data .green:focus, .data .green:active {
  border-color: #61aa73d3;
  outline: none;
}

.data .lavender:hover, .data .lavender:focus, .data .lavender:active {
  border-color: #a57abebb;
  outline: none;
}

.data .blue:hover, .data .blue:focus, .data .blue:active {
  border-color: #4baccac0;
  outline: none;
}

.data .button {
  margin-top: 30px;
  margin-bottom: 30px;
}

.data .button__delivery {
  letter-spacing: .04em;
  color: #fff;
  cursor: pointer;
  max-width: 200px;
  background-color: #ffb8ca;
  border: 1px solid #ffb8ca;
  border-radius: 5%;
  padding: 20px;
  font-family: Titan One, sans-serif;
  font-size: 13px;
  box-shadow: 0 1px 1px #d41443, 0 1px 7px #b16477;
}

@media (min-width: 480px) {
  .main__overlay {
    max-width: 600px;
  }

  .first-label {
    justify-content: space-around;
    display: flex;
  }

  .filling {
    width: 380px;
  }
}

@media (min-width: 768px) {
  .form__label-first {
    justify-content: space-around;
    display: flex;
  }

  .modal {
    max-width: 500px;
  }

  .form__list {
    margin-left: 50px;
  }
}

@media (min-width: 1200px) {
  .main__overlay {
    margin-top: 100px;
  }

  .data__container {
    width: 300px;
  }
}

.customer-review {
  padding: 0 20px 86px;
  font-family: Dm sans, sans-serif;
}

@media screen and (min-width: 768px) {
  .customer-review {
    padding-bottom: 119px;
  }
}

@media screen and (min-width: 1280px) {
  .customer-review {
    padding-bottom: 132px;
  }
}

.customer-review__customer-image {
  width: 85px;
  height: 85px;
  margin: 0 auto;
  display: block;
}

.customer-review__box {
  background: #fafafa;
  margin: 29px auto 8px;
  padding: 43px 66px 26px 57px;
}

@media screen and (min-width: 768px) {
  .customer-review__box {
    max-width: 620px;
    background: #fafafa66;
    padding: 47px 0 34px;
