  z-index: 0;
  color: #000;
  text-align: center;
  background-image: repeating-linear-gradient(#fafafa, #fafafa 29px, #e1e1e1 30px);
  font-size: 12px;
  font-weight: 500;
  line-height: 30.6px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .customer-review__review {
    letter-spacing: .04em;
    max-width: 460px;
    background-image: repeating-linear-gradient(#fafafa66, #fafafa66 29px, #e1e1e1 30px);
    margin: 0 auto;
    font-size: 14px;
    line-height: 30px;
  }
}

@media screen and (min-width: 1280px) {
  .customer-review__review {
    letter-spacing: .04em;
    max-width: 750px;
    background-image: repeating-linear-gradient(#fafafa, #fafafa 29px, #e1e1e1 30px);
    font-size: 16px;
    line-height: 30px;
  }
}

