.customer-review__signature {
  text-align: center;
  color: #000;
  font-family: Dm sans, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 26px;
  position: relative;
}

.customer-review__signature:before {
  content: ". . .";
  color: #d41443;
  margin-bottom: 18px;
  font-size: 36px;
  display: block;
}

.customer-review__button {
  content: "";
  cursor: pointer;
  width: 12px;
  height: 12px;
  background-color: #d9d9d9;
  border: none;
  border-radius: 50%;
  transition: all cubic-bezier(.4, 0, .2, 1);
